import axios from 'axios'
const state = {
    cartItems: []
}

const mutations = {
    UPDATA_CART_ITEMS (state,payload) {
        state.cartItems = payload
    }
}
const actions = {
    getCarItems (context) {
        axios.get('/api/cart').then(response => {
            context.commit('UPDATA_CART_ITEMS',response.data)
        })
    },
    removeAllItem (context) {
        axios.post('/api/cart/delete/all').then(function(response){
            context.commit('UPDATA_CART_ITEMS',response.data)
        })
    },
    addCartItem(context,productItem) {
        axios.post('/api/cart',productItem).then(response => {
            context.commit('UPDATA_CART_ITEMS',response.data)
        })
    },
    removeItem(context,cartItem) {
        axios.post('/api/cart/delete',cartItem).then(res => {
            context.commit('UPDATA_CART_ITEMS',res.data)
        })
    },
}

const getters = {
    cartItems: state => state.cartItems,
    cartQuantity: function(state) {
        return state.cartItems.reduce(function(a,b){
            return b.quantity + a
        },0)
    },
    cartPrice: function(state) {
        return state.cartItems.reduce(function(a,b){
            return (b.price * b.quantity) + a
        },0).toFixed(2)
    }
}
const cartModule = {
    state,
    mutations,
    actions,
    getters
}

export default cartModule;