<template>
    <div>
        <p class="cart-item-title is-inline">{{cartItem.title}}</p>
        <div class="is-pulled-right">
            <i class="fa fa-arrow-circle-up cart-item-modify" @click="addCartItem(cartItem)"></i>
            <i class="fa fa-arrow-circle-down cart-item-modify" @click="removeItem(cartItem)"></i>
        </div>
        <div class="cart-item-content">
            <span class="cart-item-price has-text-primary has-text-weight-bold">
                单价： {{cartItem.price}}￥
            </span>
            <span class="cart-item-quantity has-text-grey is-pulled-right">
                数量： {{cartItem.quantity}}
            </span>
        </div>
    </div>
</template>
<script>
export default {
    name: 'CartListItem',
    props: ['cartItem','productItem'],
    methods: {
        addCartItem(cartItem) {
            this.$store.dispatch('addCartItem',cartItem)
        },
        removeItem(cartItem) {
            this.$store.dispatch('removeItem',cartItem)
        }
    }
}
</script>
<style lang="less" scoped>
.cart-item-modify {
    cursor: pointer;
    margin: 0 1px;
}
</style>

