<template>
  <div id="cart">
    <div class="cart-header has-text-centered">
      <i class="fa fa-2x fa-shopping-cart"></i>
    </div>
    <p v-if="!cartItems.length" class="cart-empty-text has-text-centered">添加一些商品到购物车</p>
    <ul v-if="cartItems.length > 0">
      <li class="cart-item" v-for="cartItem in cartItems" :key="cartItem.id">
        <CartListItem :cartItem="cartItem" />
      </li>
      <div class="cart-details">
        <p>总数量：
          <span class="has-text-weight-bold">{{cartQuantity}}</span>
        </p>
        <p class="cart-remove-all-text" @click="removeAllItem">
          <i class="fa fa-trash"></i>清空购物车
        </p>
      </div>
    </ul>
    <button class="button is-primary" @click="alert">结算（
      <span class="has-text-weight-bold">￥{{cartPrice}}</span>）
    </button>
  </div>
</template>
<script>
import { mapGetters,mapActions } from "vuex";
import CartListItem from "./CartListItem.vue";
export default {
  name: "CartList",
  computed: {
    ...mapGetters(["cartItems","cartQuantity","cartPrice"]),
  },
  created() {
    this.$store.dispatch("getCarItems");
  },
  methods: {
    ...mapActions(['removeAllItem']),
      alert(){
        alert('这是写着玩的')
      }
      
  },
  components: {
    CartListItem
  }
};
</script>
<style lang="less" scoped>
#cart {
  height: 100%;
  padding: 30px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.cart-empty-text {
  padding: 10px 0px;
}
.cart-header {
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 15px;
}
.cart-item {
  padding: 10px 0;
}
.cart-details {
  font-size: 12px;
  display: flex;
  justify-content: space-between;
  padding: 15px;
}
.cart-remove-all-text {

  cursor: pointer;
}
.cart-remove-all-text .fa {
  padding-right: 5px;
}
</style>

