import axios from 'axios'
const state = {
    productItems: []
}

const mutations = {
    UPDATA_PRODUCT_ITEMS (state,productItems) {
        state.productItems = productItems;
    }
}

// 获取所有的产品信息
const actions = {
    getProductItems({commit}) {
        axios.get('/api/products').then(response => {
            commit('UPDATA_PRODUCT_ITEMS',response.data)
        })
    }
}

const getters = {
    productItems: state => state.productItems
}
const productModule = {
    state,
    mutations,
    actions,
    getters
}

export default productModule;